import Vue from "vue";
import VueRouter from "vue-router";
// 1.  () 
//  import 
import Home from "../../components/game-index.vue";

Vue.use(VueRouter);

// 2. 
// 
const routes = [{
		path: "/",
		name: "PGame",
		component: Home,
	},
	{
		path: "/verify",
		name: "verify",
		// 
		component: () =>
			import("../../components/verify.vue"),
	},
	{
		path: "/verifyDetails",
		name: "verifyDetails",
		// 
		component: () =>
			import("../../components/verify-details.vue"),
	},
];

// 3.  router  `routes` 
const router = new VueRouter({
	// mode: "history",
	// base: process.env.BASE_URL,
	routes,
});

export default router;