import Vue from 'vue'
import App from './App.vue'
import router from "./assets/js/router.js"
import service from '../src/assets/js/http.js'

Vue.prototype.$http = service


function setFaviconByBrand() {
	const urlParams = new URLSearchParams(window.location.search)
	const brandName = urlParams.get('brandName') || 'kf'
	document.title = `${brandName.toUpperCase()}Game`

	// public 
	const iconPath = `${process.env.VUE_APP_IMG_URL}/${brandName}/favicon.ico`

	let link = document.querySelector("link[rel~='icon']")
	if (!link) {
		link = document.createElement('link')
		link.rel = 'icon'
		document.head.appendChild(link)
	}
	link.href = iconPath + '?v=' + Date.now()
}

setFaviconByBrand()

//  vue-i18n
import VueI18n from 'vue-i18n'
import pt from '../src/assets/js/lang/pt.js'
import en from '../src/assets/js/lang/en.js'
Vue.use(VueI18n)

// 
const messages = {
	en: en,
	pt: pt
}

//  URL  ?language=
const urlParams = new URLSearchParams(window.location.search)
const languageParam = urlParams.get('language')
const supportedLangs = ['en', 'pt']
const language = supportedLangs.includes(languageParam) ? languageParam : 'pt'

//  i18n 
const i18n = new VueI18n({
	locale: language,
	fallbackLocale: 'en',
	messages,
	// 
	interpolation: {
		prefix: '%{', //  %{
		suffix: '}' //  }
	}
})

//   window JS 
window.i18n = i18n

import VueScroll from 'vuescroll'
Vue.use(VueScroll)

Vue.config.productionTip = false

//  Vue 
new Vue({
	router,
	i18n,
	render: h => h(App)
}).$mount('#app')